.projectContainer {
    width: 400px;
    height: 275px;
    margin: 10px;
    margin-bottom: 30px;
    border: 2px solid gray;
    border-radius: 10px;
    display: flex;
    flex-direction: column;

}

.projectContainer:hover {
    border: 3px solid  #00b812;
    cursor: pointer; 
}


.imageContainer {
    display: flex;
    width: 400px;
    height: 200px;
    margin-top: 15px;
    justify-content: center;
}


.textContainer {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 75px;
    
}


.titleContainer {
    display: flex;
    flex: 1;
    border-top: 2px solid gray; 
    border-radius:4px;
    margin: 0;
}
.title {
    margin: 0;
    margin-left: 4px;
    font-weight: bold;
    font-size: 20px;
    font-family: "Poppins";
    margin-left: 15px;
    margin-top: 5px;
    color: #176287;
}

.descriptionContainer{
    display: flex;
    flex: 1;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
}

.description {
    margin: 0;
    margin-left: 5px;
    font-size: 13px;
    font-family: "Poppins";
    border-radius: 2px;
    margin-left: 15px;
}

.userContainer {
    width: 47px;
    height: 37px;
}


.circle {
    width:30px;
    height: 30px;
    background-color:  #176287;
    margin-right: 5px;
    border-radius: 50%;
    margin-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #000;
}

.initials {
    font-size: 20px;
    color: white;
    font-weight: bold;
}







