.panel-content {
    margin: 1%;
    padding: 1%;
    text-align: left;
}

.panel-content td {
    margin: 1%;
    padding: 1%;
}

.panel-content td p {
    min-width: 80%;
    font-size: 17px;
    padding-top: 8%;
    padding-right: 3%;
    text-align: right;

}


.projectLabel {
    font-weight: bold;
    font-size: 20px;
    font-family: "Poppins";
    color: #176287;

}

.projectTitle {
    font-weight: bold;
    font-size: 17px;
    font-family: "Poppins";
    // color: #176287;
}

.inputLabel {
    color: #000;
}

.textColor {
    color: #000;
}

.containerForProjectAndName {
    width: 280px;
    height: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}


