
html, body {
    height: 100%;
}





.mainContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 100%;
    height: 100%;
    min-height: 100%;
    background-color: #2F303A;
    display: flex;
    flex-direction: column;
}

.content {
    height: 100%;
    display: flex;
    flex-direction: row;
}

.imageContainer {
    display: flex;
    flex: 1;
    align-items: center;
    // justify-content: flex-end;
    justify-content: center;
}

.titlesContainer {
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
}



.titles {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 700px;
    height: 180px;
    @media screen and (max-width: 1599px) and (min-width: 990px){
        width: 700px;
        height: 180px;
    }

    @media screen and (max-width: 2000px) and (min-width: 1600px) {
        width: 800px;
        height: 250px;
    }
}




.subTitle {
    color: #fff;
    font-family: "Poppins";
    margin: 0;
    font-size: 31px;
    margin-top: 20px;
    margin-left: 4px;

    @media screen and (max-width: 1599px) and (min-width: 990px){
        font-size: 31px;
        margin-top: 20px;
        margin-left: 4px;
    }

    @media screen and (max-width: 2000px) and (min-width: 1600px) {
        font-size: 35px;
        margin-top: 25px;
        margin-left: 4px;
    }
} 

.mainTitle {
    margin: 0;
    color: #fff;
    font-family: "Poppins";
    
    @media screen and (max-width: 1599px) and (min-width: 990px){
        font-size: 51px;
    }

    @media screen and (max-width: 2000px) and (min-width: 1600px) {
        font-size: 65px;
    }
}




.buttonsContainer {
    width: 700px;
    height: 200px;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-around;
    // background-color: greenyellow;
}

.buttonsStyle {
     background-color: "#176287";
     width: 208px;
     height: 89px;
     color: #fff;
    font-family: "Poppins";
    font-size: 20;
}

.image {

    @media screen and (max-width: 2000px) and (min-width: 1600px) {
        width: 800px;
        height: 650px;
    }

    @media screen and (max-width: 1599px) and (min-width: 990px){
        width: 600px;
        height: 500px;
    }

}






