.mainContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 100%;
    height: 100%;
    min-height: 100%;
    background-color: #2F303A;
    display: flex;
    flex-direction: column;
}


.content {
    height: 100%;
    display: flex;
    flex-direction: row;
}

.authContainer {
    display: flex;
    flex: 1;
    // background-color: indianred;
    align-items: center;
    justify-content: center;
}

.titleContainer {
    display: flex;
    flex: 1;
    // background-color: indigo;
    align-items: center;
    justify-content: flex-end;
}


.signupBox{
    width: 500px;
    height: 500px;
    background-color: #202027;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
}

.inputContainer {
    width: 450px;
}

.buttonContainer{
    width: 450px;
    height: 200px;
    margin-top: 20px;
}

.inputStyle {
    color: #fff;
}

.textInput {
    width: 440px;
    height: 40px;
    border-width: 1px;
    background-color: transparent;
    border-style: solid;
    border-color: #1762A7;
    border-radius: 5px;
    color: #fff;
    font-family: "Poppins";
}

.textInput:focus {
    width: 440px;
    height: 40px;
    border-width: 1px;
    background-color: transparent;
    border-style: solid;
    border-radius: 5px;
    color: #fff;
    font-family: "Poppins";
}

.submitInput {
    width: 450px;
    height: 50px;
    background-color: #176287;
    color: #fff;
    border-radius: 5px;
    border-color: #176287;
    border-style: none;
    border-width: 0px;
    font-size: 20px;
    // font-weight: bold; 
}

.submitInput:hover {
    cursor: pointer
}

.titleBox {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
    width: 400px;
    // background-color: khaki;
}

.title {
    font-size: 80px;
    font-family: "Poppins";
    color: #176287;
    font-weight: bold;

}